<script type="text/ecmascript-6">
  import {
    addTeacher,
    deleteTeacher,
    edu_url,
    getCustomers,
    getCustomersByPage,
    resetPWD,
    updateTeacher
  } from '@/common/js/request';

  export default {
    data() {
      return {
        // 
        all: [
          {id: ''}, // 
          {teacherName: ''}, // 
          {teacherTitle: ''}, // 
          {gender: ''}, // 
          {teacherContact: ''} // 
        ],
        // 
        allTmp: [],
        // 
        allCurTmp: null,
        // 
        form: {
          teacherNo: '',
          teacherName: '',
          teacherTitleId: '',
          gender: '',
          teacherContact: ''
        },
        // 
        bianjiForm: {
          teacherNo: '',
          teacherName: '',
          teacherTitleId: '',
          gender: '',
          teacherContact: ''
        },
        importVisible: false,//
        dialogFormVisible: false,
        formLabelWidth: '80px',
        resetVisible: false, //
        deleteVisible: false, //
        multipleSelection: [], //
        bianjiFormVisible: false,// 
        searchQuery: '', // 
        uploadHeaders: {
          'Authorization': 'Bearer ' + (localStorage['token'] ? localStorage['token'] : '')
        },
        resetNo: '',
        // 
        p_currentPage: 1,
        p_pageSizes: [5, 10, 15, 20, 30, 50, 100],
        p_pageSize: 5,
        p_total: 0,
        actionUrl:null,
        filelist:[],
        errorData:[],
        // addRules :{
        //   teacherNo :[{ required: true,message: '', trigger: 'blur' }],
        //   teacherName :[{ required: true,message: '', trigger: 'blur' }],
        //   teacherTitleId :[{ required: true,message: '', trigger: 'blur' }],
        //   gender :[{ required: true,message: '', trigger: 'blur' }],
        //   teacherContact :[{ required: true,message: '', trigger: 'blur' }]
        // },
        // editRules:{
        //   teacherNo :[{ required: true,message: '', trigger: 'blur' }],
        //   teacherName :[{ required: true,message: '', trigger: 'blur' }],
        //   teacherTitleId :[{ required: true,message: '', trigger: 'blur' }],
        //   gender :[{ required: true,message: '', trigger: 'blur' }],
        //   teacherContact :[{ required: true,message: '', trigger: 'blur' }]
        // },
        loading:false
      }
    },
    created() {
      // "http://192.168.137.185:8080/admin/teacher/batchCreation"
      this.actionUrl = "/admin/teacher/batchCreation";
      //this._getTeacherByLimit();


    },
    activated () {
      this.searchQuery="";
      this._getCustomers();
    },
    methods: {
      //
      _Refresh:function () {
        this.searchQuery="";
        this._getCustomers();
        // 5
        this.p_pageSize=5
      },
      // 
      _getCustomers: function () {
        if(this.searchQuery.length>45){
          this.$message({
            type:'error',
            message:'45'
          });
          return;
        }
        // 5
        this.p_pageSize=5
        getCustomers().then(res => {
          if(res.data=="alreadyResetPassword"){
            this.$message({
              type:'warning',
              message:''
            });
            return;
          }
          this.allTmp = res.data.data.teacherInfoList;
          this._filterData()
          this._initPageInfo()
        })
      },
      // 
      _initPageInfo: function () {
        this.p_total = this.allCurTmp.length
        this.p_currentPage = 1
        this.all = this.allCurTmp.slice(0, this.p_pageSize)
      },
      // filter data
      _filterData: function () {
        if (this.searchQuery === '') {
          this.allCurTmp = this.allTmp
          return
        }
        let filterTableDataEnd = []
        this.allTmp.forEach((value, index) => {
          if (
              (value.teacherName && value.teacherName.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0) ||
              (value.teacherNo && value.teacherNo.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0) ||
              (value.teacherTitle && value.teacherTitle.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0) ||
              (value.gender && value.gender.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0) ||
              (value.teacherContact && value.teacherContact.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0)
          ) {
            filterTableDataEnd.push(value)
          }
        });
        this.allCurTmp = filterTableDataEnd
      },
      // 
      handleSizeChange(val) {
        this.p_pageSize = val
        this._initPageInfo()
        console.log(":"+ val)
      },
      handleCurrentChange(val) {
        this.p_currentPage = val
        this.all = this.allCurTmp.slice((val - 1) * this.p_pageSize, (val) * this.p_pageSize)
      },
      // 
      select: function () {
        if(this.searchQuery.length>45){
          this.$message({
            type:'error',
            message:'45'
          })
          return;
        }
        this._filterData();
        this._initPageInfo();
      },
      addTeacher: function () {
        this.form = {};
        this.dialogFormVisible = true;
      },
      checkModifyPass:function(){
        console.log(this.multipleSelection.length);
        if (this.multipleSelection.length === 0){
          this.$message({
            message: '',
            type: 'warning'
          });
        } else {
          console.log(this.multipleSelection)
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            // for(let [idx,item] of this.multipleSelection.entries()){
            //   let userId=item.id;
            //   resetPWD(userId).then(res=>{
            //     if(res.data.errorCode==0){
            //       this.$message({
            //         type: 'success',
            //         message: '!  12345678'
            //       });
            //       this._Refresh();
            //     }
            //   });
            // }
            // this.resetVisible = true;
            this.modifyPass()
            let restNoTmp = ''
            for (let [idx, item] of this.multipleSelection.entries()) {

              restNoTmp += ',' + item.teacherNo;
            }
            this.resetNo = restNoTmp.substr(1)
          }).catch(() => {
          });



        }
      },
      modifyPass:function(){
        console.log(this.multipleSelection.length);
        //
        var mulit=JSON.parse(JSON.stringify(this.multipleSelection));
        this._reSetPwd(mulit);
  /*      for(let[idx,item] of this.multipleSelection.entries()){
          //
          resetPWD(item.id).then(res=>{
            if(res.data.errorCode == 0){
              if(idx==this.multipleSelection.length){
                this.$message({
                  title:"",
                  message:"",
                  type:"success"
                });
              }
            }else {
              this.$message({
                type: 'error',
                message: res.data.message
              });
            }
          });
          this._getCustomers();
          this.resetVisible = false;

        }*/
      },
      _reSetPwd:function (mulit) {
        resetPWD(mulit.splice(-1)[0].id).then(res=>{
          if(res.data.errorCode!=0){
            this.$message({
              type: 'error',
              message: res.data.message
            });
            this._Refresh();
          } else {
            if(mulit.length==0) {
              this.$message({
                title:"",
                message:"!  12345678",
                type:"success"
              });
              this._Refresh();
             // this._getCustomers();
              this.resetVisible = false;
            }
            if(mulit.length==0){
              return;
            }
            this._reSetPwd(mulit);
          }
        });
      },

      // 
      handleSelectionChange(val) {
        //
        this.multipleSelection = JSON.parse(JSON.stringify(val));
        //Object.assign(this.multipleSelection,val);
        this.multipleSelection.map(item=>{
          if(item.teacherTitle==""){
            item.teacherTitleId="1";
          } else if(item.teacherTitle==""){
            item.teacherTitleId="2";
          } else if(item.teacherTitle==""){
            item.teacherTitleId="3";
          }
          if(item.gender == '') {
            item.gender = "1";
          }else if(item.gender == ''){
            item.gender = "2";
          }
        });
      },
      // 
      createUser: function () {
        if (!this._validateForm(this.form)) {
          return
        }
        addTeacher(this.form).then(res => {
          //console.log(res)
          if (res.data.errorCode === 0) {
            this.$message({
              type: 'success',
              message: '!'
            });
            this._Refresh();
            //this._getCustomers();

          } else {
            this.$message({
              type: 'error',
              message: res.data.message
            });
            this._Refresh();
          }
          this.dialogFormVisible = false;
        });


      },
      // 
      deletedata:function () {
        if(this.multipleSelection.length==0){
          // alert("")
          this.$message({
            message: '',
            type: 'warning'
          });
        }
        else{
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
            center: true
          }).then(() => {
            for(let [idx,item] of this.multipleSelection.entries()){
              //console.log(item.id);
              deleteTeacher(item.id).then(res => {
                console.log(res);
                if(res.status==200){
                  if(res.data.errorCode!=0){
                    // 
                    this.$message({
                      type: 'error',
                      message: res.data.message
                    });
                    this._Refresh();
                  } else {
                    if(idx==this.multipleSelection.length-1) {
                      this.$message({
                        type: 'success',
                        message: '!'
                      });
                      this._Refresh();
                    }
                  }
                }
              })
            }
          }).catch(() => {
          });
        }
      },
      // 
      bianjiUser:function () {
        if (this.multipleSelection.length === 1) {
          // 
          this.bianjiFormVisible= true;
          // 
          Object.assign(this.bianjiForm,this.multipleSelection[0]);
        }
        else {
          // alert(",")
          this.$message({
            message: '',
            type: 'warning'
          });
        }
      },
      _validateForm: function (form) {
        // 
        let errorMsg = null


        if (!form.teacherNo || form.teacherNo.trim().length === 0) {
          errorMsg = ''
        } else if (form.teacherNo.trim().length > 45) {
          errorMsg = '45'
        }else if((/^\s+/).test(form.teacherNo)){
          errorMsg = ''
        }else if((/\s+$/).test(form.teacherNo)){
          errorMsg = ''
        } else {
         // var regex = /^[0-9]+$/;
          var regex = /^\+?[0-9]\d*$/;
          if(!regex.test(form.teacherNo.trim())){
            errorMsg = "0";
          }
        }
        if (errorMsg && errorMsg.trim().length > 0) {
          this.$message.error(errorMsg)
          return false
        }
        // 
        if (!form.teacherName || form.teacherName.trim().length === 0) {
          errorMsg = ''
        } else if (form.teacherName.trim().length > 45) {
          errorMsg = '45'
        }else if((/^\s+/).test(form.teacherName)){
          errorMsg = ''
        }else if((/\s+$/).test(form.teacherName)){
          errorMsg = ''
        }
        if (errorMsg && errorMsg.trim().length > 0) {
          this.$message.error(errorMsg)
          return false
        }
        // 
        if (!form.teacherTitleId) {
          errorMsg = ''
        }
        if (errorMsg && errorMsg.trim().length > 0) {
          this.$message.error(errorMsg)
          return false
        }
        // 
        if (!form.gender) {
          errorMsg = ''
        }
        if (errorMsg && errorMsg.trim().length > 0) {
          this.$message.error(errorMsg)
          return false
        }
        // 
        // var email=/^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/;
        var email=/^[A-Za-z0-9\u4e00-\u9fa5+\.+\-+\_]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        var phone= /^1\d{10}$/;
        var kong=/^\S+$/;
        if(!form.teacherContact || form.teacherContact.trim().length === 0){
          errorMsg=""
        }else if(!kong.test(form.teacherContact)){
          errorMsg=""
        } else if (form.teacherContact.trim().length > 45) {
          errorMsg="45"
        }else{
          if(email.test(form.teacherContact) ||  phone.test(form.teacherContact)){
            return true
          }
          else{
            if(form.teacherContact.indexOf("@") != -1){
              if(!email.test(form.teacherContact)){
                errorMsg=""
              }
            }
             if(!phone.test(form.teacherContact)){
              errorMsg=""
            }
          }
        }
        if (errorMsg && errorMsg.trim().length > 0) {
          this.$message.error(errorMsg)
          return false
        }
        return true
      },
      // 
      bianjiUser2: function () {
        if (!this._validateForm(this.bianjiForm)) {
          return
        }

        updateTeacher(this.bianjiForm.gender,this.bianjiForm.teacherContact,
          this.bianjiForm.teacherName,this.bianjiForm.teacherNo,
         this.bianjiForm.teacherTitleId,this.bianjiForm.id).then(res => {
          console.log(this.bianjiForm)
           console.log(res)
          if (res.status === 200) {
             if(res.data.errorCode == 12){
               this.$message({
                 type: 'error',
                 message: ""
               });
             } else if (res.data.errorCode !== 0 && res.data.errorCode !== 12) {
              // 
              this.$message({
                type: 'error',
                //message: ""
                message: res.data.message
              });
               this._Refresh();
            }
            else {
              this.bianjiFormVisible= false;
              //this._getCustomers();
               this._Refresh();
              // 
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          }
        })
      },
      // 
      upload: function () {

        this.uploadHeaders = {
          'Authorization': 'Bearer ' + (localStorage['token'] ? localStorage['token'] : '')
        };
        this.$refs.upload.submit();
      },
      importTeacher:function(){
        //
        this.fileList=[];
        this.errorData=[];
        this.importVisible = true;
        this.loading=false;
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },
    /*  beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },*/
      beforeUploadHandler:function(file){
        var fileName = file.name;
        if(fileName.lastIndexOf(".xls")<0 && fileName.lastIndexOf(".xlsx")<0){
          this.$message({
            title:"",
            message:"xlsxlsx",
            type:"error"
          });
          this._Refresh();
          this.loading=false;
          return false;
        }
        console.log(""+file.size);
        if(file.size>1024*21){
          this.$message({
            title:"",
            message:"20KB",
            type:"error"
          });
          this._Refresh();
          this.loading=false;
          return false;
        } else {
          this.loading = true;
          return true;
        }
      },
      uploadSuccess: function (msg) {
        console.log(msg)
        console.log(msg+""+msg.errorCode);
        this.loading = false;
        if(msg.errorCode==0){
          this.$refs.upload.clearFiles();
          if(msg.data.failureReasonList.length==0){
            //
            this.importVisible = false;
            this.$message({
              type:'success',
              message:''
            });
          } else {
            console.log("")
            this.errorData = msg.data.failureReasonList;
             console.log(this.errorData)
        /*    this.errorData.forEach((value, index) => {
              console.log(value)
              var regex = /^\+?[0-9]\d*$/;
              if(!regex.test(value.teacherNum.trim())){
                // errorMsg = "0";
                value.reason="0"
              }else if(value.teacherNum.trim().length >45){
                value.reason="45"
              }else if(value.teacherName.trim().length >45){
                value.reason="45"
              }else{
                value.reason=""
              }
            })*/
          }
          this._Refresh();
        }
        if(msg.errorCode==51){
              this.importVisible = false;
              this.fileList=[];
              this.errorData=[];
              this.$message({
                title:"",
                message:msg.message,
                type:"error"
              });
          this._Refresh();
          this.loading=false;
            }
        //this._getCustomers()
        this._Refresh();
      }
    },
    beforeRouteEnter: (to, from, next) => {
      console.log("");
      next(vm => {
        // vm._getExpirementInfo();
        // vm.handleSizeChange(5)
        vm.p_currentPage=1
        vm.p_pageSize=5
      });
    }
    // watch
    // watch: {
    //   searchQuery(){
    //     if(this.searchQuery.length>45){
    //       this.$message({
    //         title:"",
    //         message:"45",
    //         type:"error"
    //       });
    //     }
    //   }
    // }
  }
</script>

