<template>
  <div id="share" style="border: 1px solid #dcdfe6;width: 100%;">
    <!--<iframe :src="fileManageUrl"-->
    <!--style="width: 100%;height: 600px;-->
    <!--overflow-y:auto;overflow-x: hidden;" id="fileSystem"></iframe>-->
    <div style="padding: 15px;">
      <el-form :inline="true"  class="demo-form-inline">
        <el-form-item >
          <el-button size="small" icon="el-icon-download" :disabled="isDown" @click="downloadFile"></el-button>
          <el-button size="small" icon="el-icon-close" @click="deleteFile" :disabled="isDelete"></el-button>
          <el-button size="small" icon="el-icon-rank" :disabled="isMove" @click="moveFile"></el-button>
          <el-button size="small" icon="el-icon-edit" :disabled="isrename" @click="renameFile"></el-button>
          <!--<el-button size="small" icon="el-icon-upload2" :disabled="isPack" @click="packFile"></el-button>-->
        </el-form-item>
        <el-form-item style="float: right">
          <el-button size="small" icon="el-icon-upload" @click="upload"></el-button>
          <el-button size="small" icon="el-icon-tickets" @click="newFile"></el-button>
        </el-form-item>
      </el-form>
      <!---->
      <div class="title">
        <img src="../../../static/images/icon/icon-home.png" width="18" height="18" alt="" class="icon">
        <a href="javascript:void(0);" style="color: #337ab7" @click="_getShareFileList('','','')"></a>
        <el-tooltip placement="top" >
          <div slot="content">{{path}}</div>
          <el-tag type="success" size="mini" id="mulu" >{{path}}</el-tag>
        </el-tooltip>
      </div>

      <el-table id="Table"
                border
                :data="tableData"
                @selection-change="handleSelectionChange"
                style="width: 100%;text-align: center">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="180">
          <template slot-scope="scope">
            <a href="javascript:void(0);" @click="nextPath(scope.row)"><i :class="{'el-icon-document':scope.row.type==1,'el-icon-goods':scope.row.type==2}"></i>{{scope.row.name}}</a>
          </template>
        </el-table-column>
        <el-table-column
          prop="size"
          label="">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{parseFloat(scope.row.size/1024) > 1 ? (parseFloat(scope.row.size/1024/1024) >1 ? ((parseFloat(scope.row.size/1024/1024)).toFixed(2).concat('M')) : (parseFloat(scope.row.size/1024)).toFixed(2).concat('K')) : (parseFloat(scope.row.size)).toFixed(2).concat('B')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="time"
          label="">
        </el-table-column>
      </el-table>

      <!---->
      <el-dialog
        title=""
        :visible.sync="uploadVisible"
        width="40%">
        <p style="margin-bottom: 20px;">
          <span>:</span>
          <span style="margin-left: 5px;word-break: break-all">{{uploadPath}}</span>
        </p>
        <el-upload
          class="upload-demo"
          ref="upload"
          :action="actionUrl"
          :limit="1"
          :multiple="false"
          :data="uploadData"
          :auto-upload="false"
          :on-success="uploadSuccess"
          :file-list="fileList"
          :http-request="customUploadHandler"
          :on-exceed="exceedHandler"
          v-loading="loading">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
        <span slot="footer" class="dialog-footer">
          <el-button @click="uploadVisible = false"> </el-button>
          <el-button type="primary" @click="submitUpload" :disabled="!canUpload"> </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="newVisible"
        width="38%" style="border-radius: 5%">
        <p style="margin-bottom: 20px;">
          <span>:</span>
          <span style="margin-left: 5px;word-break: break-all">{{newUploadPath}}{{newPath}}</span>
        </p>
        <h3></h3>
        <el-input
          placeholder=""
          v-model="newPath"
          clearable>
        </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button @click="newVisible = false"> </el-button>
          <el-button type="primary" @click="newFileSave()"> </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="moveVisible"
        width="38%" style="border-radius: 5%">
        <h1 style="margin-bottom: 10px;"></h1>
        <el-input
          placeholder=""
          v-model="targetPath"
          clearable>
        </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button @click="moveVisible = false"> </el-button>
          <el-button type="primary" @click="saveMoveFile()"> </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="renameVisible"
        width="38%" style="border-radius: 5%">
        <h1 style="margin-bottom: 10px;"></h1>
        <el-input
          placeholder=""
          v-model="newFileName"
          clearable>
        </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button @click="renameVisible = false"> </el-button>
          <el-button type="primary" @click="renameShareFile()"> </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="packVisible"
        width="38%" style="border-radius: 5%">
        <h1 style="margin-bottom: 10px;"></h1>
        <el-input
          placeholder=""
          v-model="newInput"
          clearable>
        </el-input>
        <el-checkbox-group style="margin-top: 20px;">
          <el-checkbox label="" ></el-checkbox>
        </el-checkbox-group>

        <span slot="footer" class="dialog-footer">
          <el-button @click="packVisible = false"> </el-button>
          <el-button type="primary" @click="packVisible = false"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { getShareFileList,edu_url,deleteShareFile,downloadShareFile,createShareDirectory,renameShareFile,moveShareFile,checkIsExistsShare} from '@/common/js/request';
  import axios from 'axios';
  export default {
    data (){
      return{
        tableData: [{
          type: '',
          name: '',
          size: '',
          time: ''
        }],
        formInline: {},
        userid:'',
        courseId:'',
        uploadHeaders: {
          'Authorization': 'Bearer ' + (localStorage['token'] ? localStorage['token'] : '')
        },
        uploadData:{},
        path:"/",
        uploadPath:"/",
        newUploadPath:"",
        targetPath:"",
        uploadVisible:false,
        actionUrl:null,
        newFileName:null,
        fileList: [],
        newVisible:false,
        newInput:"",
        newPath:"",
        isDown:true,
        isDelete:true,
        isMove:true,
        isrename:true,
        isPack:true,
        moveVisible:false,
        renameVisible:false,
        packVisible:false,
        canUpload:true,
        loading:false
      }
    },

    /* activated(){
       var courseId=this.$route.params.courseId;
       console.log(courseId+"");
       if(courseId==null || courseId==undefined){
         this.userid=localStorage['user'] ? JSON.parse(localStorage['user']).userId : '';
       }else{
         this.userid=courseId;
       }
       console.log(this.userid+"");
     },*/
    methods:{
      handleDelete(index, row) {
        console.log(index, row);
      },
      submitUpload() {
        if(this.$refs.upload.uploadFiles.length==0){
          this.$message({
            type:'warning',
            message:''
          });
          return;
        }
        this.loading = true;
        this.$refs.upload.submit();
      },
      //
      exceedHandler(files,fileList){
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      uploadSuccess:function(response){
        console.log("");
        this.loading = false;
        this.canUpload=true;
        this.fileList=[];
        this.$refs.upload.clearFiles();
        if(response=="success"){
          if(this.path.trim().length==0){
            this.path = "/";
          }
          var userId = "";
          if(this.userid!=null && this.userid!=""){
            userId=this.userid;
          }else{
            userId=this.courseId;
          }
          this._getShareFileList(userId,this.path);
        }
      },
      //
      upload(){
        this.loading=false;
        this.canUpload=true;
        console.log(this.path+"");
        console.log(this.multipleSelection);
        this.fileList=[];
        if(this.multipleSelection!=null && this.multipleSelection.length>0){
          if(this.multipleSelection.length>1){
            this.$message({
              type:"error",
              message:"!"
            })
            return;
          }
          for(let[idx,item] of this.multipleSelection.entries()){
            if(item.type!=2){
              this.$message({
                type:"error",
                message:"!"
              })
              return;
            }
            if(item.type==2){
              if(this.path=="/"){
                this.uploadPath=this.path+item.name;
              }else{
                this.uploadPath=this.path+"/"+item.name;
              }
            }
          }

        }else{
          console.log("");
          this.uploadPath=this.path;
        }
        console.log(this.path);
        this.uploadVisible=true
      },
      newFile(){
        this.newPath="";
        this.newUploadPath="";
        if(this.multipleSelection!=null && this.multipleSelection.length>0){
          if(this.multipleSelection.length>1){
            this.$message({
              type:"error",
              message:"!"
            })
            return;
          }
          for(let[idx,item] of this.multipleSelection.entries()){
            if(item.type!=2){
              this.$message({
                type:"error",
                message:"!"
              })
              return;
            }
            if(item.type==2){
              if(this.path=="/"){
                this.newUploadPath=this.path+item.name+"/";
              }else{
                this.newUploadPath=this.path+"/"+item.name+"/";
              }
            }
          }

        }else{
          if(this.path=="/"){
            this.newUploadPath=this.path;
          }else{
            this.newUploadPath=this.path+"/";
          }

        }
        this.newVisible=true
      },
      //
      renameShareFile(){
        var fileName=this.newFileName.trim();
        if(fileName==this.multipleSelection[0].name){
          this.$message({
            type:"success",
            message:"!"
          });
          this._getShareFileList(paramId,this.path,type);
          this.renameVisible=false;
          return;
        }
        if(fileName==null || fileName==""){
          this.$message({
            type:"error",
            message:"!"
          });
          return;
        }
        /*var fileLength=this.getByFileLen(fileName);
        console.log(""+fileLength);*/
        if(fileName.length>80){
          this.$message({
            title: "",
            message: "80!",
            type: 'error'
          });
          return;
        }
        var Filepatt=/^:|\*|\?|\"|\/|\\|<|>|\|$/;
        console.log(fileName+"!")
        if(Filepatt.test(fileName)){
          this.$message({
            type:"error",
            message:"(: * ? \" > < | \\ /)!"
          });
          return;
        }
        var patt=/^(\.)+$/;
        if(patt.test(fileName)){
          this.$message({
            type:"error",
            message:"!"
          });
          return;
        }
        //var oldName=this.multipleSelection[0].name;
        //var suffix=oldName.substring(oldName.indexOf("."),oldName.length);
        //
        let isExist = false;
        if(typeof this.tableData != 'undefined') {
          for (let [idx, item] of this.tableData.entries()) {
            if(item.name==fileName.trim()){
              if(fileName.trim()!=this.multipleSelection[0].name){
                isExist = true;
                break;
              }
            }
          }
        }
        if(isExist){
          this.$message({
            type:"error",
            message:"!"
          });
          return;
        }


        var paramId="";
        var type=0;
        if(this.userid!=null && this.userid!=""){
          paramId=this.userid;
          type=1;//
        }else{
          paramId=this.courseId;
          type=2;//
        }
        for(let[idx,item] of this.multipleSelection.entries()){
          renameShareFile(paramId,item.name,encodeURIComponent(fileName),this.path,type).then(res=>{
            if(res.data=="alreadyResetPassword"){
              this.$message({
                type:'warning',
                message:''
              });
              return;
            }
            if(res.data=="alreadyResetPassword"){
              this.$message({
                type:'warning',
                message:''
              });
              return;
            }
            if(res.status==200){
              if(res.data.errorCode==0 && res.data.data=="0"){
                this.$message({
                  type:"success",
                  message:"!"
                });
                this._getShareFileList(paramId,this.path,type);
              }else if(res.data.errorCode==0 && res.data.data=="1"){
                this.$message({
                  type:"error",
                  message:"!"
                });
              }else if(res.data.errorCode==0 && res.data.data=="2"){
                this.$message({
                  type:"error",
                  message:"!"
                });
              }else if(res.data.errorCode==42){
                this.$message({
                  type:"error",
                  message:"!"
                });
              }
            }
          });
        }
        this.renameVisible=false;

      },
      nextPath:function(item){
        if(parseInt(item.type)==2){
          //
          if(this.path.lastIndexOf("/")<0){
            this.path+="/";
          }
          if(this.path.lastIndexOf("/")>=0 && this.path!='/'){
            this.path += "/";
          }
          console.log(this.path);
          this.path = this.path+item.name;
          this.path = this.path.replace(/\\/g,"/");
          if(this.userid!=null && this.userid!=""){
            this._getShareFileList(this.userid,this.path,1);
          }else{
            this._getShareFileList(this.courseId,this.path,2);
          }

        }
      },
      //
      newFileSave(){
        var filePath=this.newPath.trim();
        if(filePath==null || filePath=="" || filePath.length<0){
          this.$message({
            type:"error",
            message:"!"
          });
          return;
        }else {
          filePath = filePath.replace(/\\/g, "/");
          /*console.log(filePath+"");
          var endStr=filePath.substr(filePath.length-1,1);
          console.log(endStr+"");
          if(endStr=="/"){
            filePath=filePath.substr(0,filePath.length-1);
          }*/
          //var dirs = filePath.split("/");
          // for (let i = 0; i < dirs.length; i++) {}
          // var dir = dirs[i];
          var patt=/^:|\*|\?|\"|\/|\\|<|>|\|$/;
          /*var fileLength=this.getByFileLen(filePath);
          console.log(""+fileLength);*/
          if (filePath.length > 80) {
            this.$message({
              title: "",
              message: "80!",
              type: 'error'
            });
            return;
          } else if (filePath==null || filePath.trim().length == 0) {
            //
            this.$message({
              title: "",
              message: "",
              type: 'error'
            });
            return;
          }else if(patt.test(filePath.trim())){
            this.$message({
              type:"error",
              message:"(: * ? \" > < | \\ /)!"
            });
            return;
          }
          var filePatt=/^(\.)+$/;
          if(filePatt.test(filePath)){
            this.$message({
              type:"error",
              message:"!"
            });
            return;
          }
          console.log(""+filePath.trim());
          console.log(this.multipleSelection);
          if(this.multipleSelection!=null && this.multipleSelection!=undefined
            && this.multipleSelection.length>0  && this.multipleSelection[0].type==2){
            if(this.path=="/"){
              this.path=this.path+this.multipleSelection[0].name;
            }else{
              this.path=this.path+"/"+this.multipleSelection[0].name;
            }

          }
          console.log("path"+this.path+filePath);
          var paramId="";
          var type=0;
          if(this.userid!=null && this.userid!=""){
            paramId=this.userid;
            type=1;
          }else{
            paramId=this.courseId;
            type=2;
          }
          createShareDirectory(paramId,encodeURIComponent(filePath.trim()),this.path,type).then(res=>{
            if(res.data=="alreadyResetPassword"){
              this.$message({
                type:'warning',
                message:''
              });
              return;
            }
            if(res.status==200){
              if(res.data.errorCode==0 && res.data.data=="1"){
                this.$message({
                  type:"success",
                  message:"!"
                });
                this._getShareFileList(paramId,this.path,type);
              }else if(res.data.errorCode==0 && res.data.data=="2"){
                this.$message({
                  type:"error",
                  message:"!"
                });
                this._getShareFileList(paramId,this.path,type);
              }
            }
          })
          //}
        }
        this.newVisible=false;

      },
      //
      saveMoveFile(){
        var filePath=this.targetPath.trim();
        if(filePath==null || filePath=="" || filePath.length<0){
          this.$message({
            type:"error",
            message:"!"
          })
        }else {
          filePath = filePath.replace(/\\/g, "/");
          var endStr=filePath.substr(filePath.length-1,1);
          console.log(endStr+"");
          //"/"
          if(endStr=="/"){
            filePath=filePath.substr(0,filePath.length-1);
          }
          var dirs = filePath.split("/");
          console.log(dirs);
          for (let i = 0; i < dirs.length; i++) {
            var dir = dirs[i];
            var patt = /^:|\*|\?|\"|\/|<|>|\|$/;
            var filePatt = /^(\.)+$/;
            if (dir.length > 80) {
              this.$message({
                title: "",
                message: "80",
                type: 'error'
              });
              return;
            } else if (dir && dir.trim().length == 0) {
              //
              this.$message({
                title: "",
                message: "",
                type: 'error'
              });
              return;
            } else if (patt.test(dir.trim())) {
              this.$message({
                type: "error",
                message: "(: * ? \" > < | )!"
              });
              return;
            }else if(filePatt.test(dir.trim())){
              this.$message({
                type: "error",
                message: "!"
              });
              return;
            }
          }
          var fileNameArry=new Array();
          //
          for(let[idx,item] of this.multipleSelection.entries()){
            if(this.path=="/"){
              fileNameArry[idx]=this.path+item.name;
            }else{
              fileNameArry[idx]=this.path+"/"+item.name;
            }
          }
          var paramId="";
          var type=0;
          if(this.userid!=null && this.userid!=""){
            paramId=this.userid;
            type=1;
          }else{
            paramId=this.courseId;
            type=2;
          }
          moveShareFile(paramId,filePath,fileNameArry,1,type).then(res=>{
            if(res.data=="alreadyResetPassword"){
              this.$message({
                type:'warning',
                message:''
              });
              return;
            }
            if(res.status==200){
              if(res.data.errorCode==0 && res.data.data=="0"){
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.moveVisible=false;
                if(filePath.substr(0,1)!="/"){
                  this.path="/"+filePath;
                }else{
                  this.path=filePath;
                }
                console.log(this.path+"");
                this._getShareFileList(paramId,this.path,type);
              }else if(res.data.errorCode==0 && res.data.data=="2"){
                this.$message({
                  type: "error",
                  message: "!"
                });
                return;
              }else if(res.data.errorCode==0 && res.data.data=="3"){
                this.$message({
                  type: "error",
                  message: "!"
                });
                return;
              }else if(res.data.errorCode==0 && res.data.data=="4"){
                this.$message({
                  type: "error",
                  message: "!"
                });
                return;
              }else if(res.data.errorCode==0 && res.data.data=="5"){
                this.$message({
                  type: "error",
                  message: "!"
                });
                return;
              }else if(res.data.errorCode==0 && res.data.data!="0"){
                this.$confirm(res.data.data+',?', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).then(() => {
                  moveShareFile(paramId,filePath,fileNameArry,2,type).then(res=>{
                    if(res.data=="alreadyResetPassword"){
                      this.$message({
                        type:'warning',
                        message:''
                      });
                      return;
                    }
                    if(res.status==200) {
                      if (res.data.errorCode == 0 && res.data.data == "0") {
                        this.$message({
                          type: "success",
                          message: "!"
                        });
                        if(filePath.substr(0,1)!="/"){
                          this.path="/"+filePath;
                        }else{
                          this.path=filePath;
                        }
                        console.log(this.path+"");
                        this.moveVisible=false;
                        this._getShareFileList(paramId,this.path,type);
                      }
                    }
                  })
                })
              }
            }
          });
        }
      },

      handleSelectionChange(val){
        this.multipleSelection = val;
        if(this.multipleSelection ==""){
          this.isDown=true
          this.isDelete=true
          this.isMove=true
          this.isrename=true
          this.isPack=true
          return
        }
        if(this.multipleSelection== val){
          this.isDown=false
          this.isDelete=false
          this.isMove=false
          this.isrename=false
          this.isPack=false
          this.multipleSelection = JSON.parse(JSON.stringify(val));
          console.log(this.multipleSelection.length+"");
          return
        }
      },
      downloadFile(){
        if(this.multipleSelection==null || this.multipleSelection.length==0){
          this.$message({
            message: '',
            type: 'warning'
          });
          return;
        }else{
          if(this.multipleSelection.length>1){
            this.$message({
              message: '',
              type: 'warning'
            });
            return;
          }else if(this.multipleSelection[0].type==2){
            this.$message({
              message: '',
              type: 'error'
            });
            return;
          }else{

            for(let [idx,item] of this.multipleSelection.entries()){
              console.log(this.multipleSelection.length+"");
              var filePath="";
              if(this.path=="/"){
                filePath=this.path+item.name;
              }else{
                filePath=this.path+"/"+item.name;
              }
              var paramId="";
              var type=0;
              if(this.userid!=null && this.userid!=""){
                paramId=this.userid;
                type=1;
              }else{
                paramId=this.courseId;
                type=2;
              }
              checkIsExistsShare(paramId,filePath,type).then(res=>{
                if(res.data=="alreadyResetPassword"){
                  this.$message({
                    type:'warning',
                    message:''
                  });
                  return;
                }
                if(res.status==200){
                  if(res.data.data!=null && res.data.data=="0"){
                    this.$message({
                      type:"error",
                      message:"!"
                    });
                    this._getShareFileList(paramId,this.path,type);
                    return;
                  }else{
                    var name=encodeURIComponent(item.name);
                    console.log(name);
                    window.location.href=edu_url+"/share/downloadShareFile?userId="+encodeURIComponent(paramId)+"&fileName="+encodeURIComponent(name)+"&path="+encodeURIComponent(this.path)+"&type="+encodeURIComponent(type);
                  }
                }
              });
            }
          }
        }


      },
      deleteFile(){
        var paramId="";
        var type=0;
        if(this.userid!=null && this.userid!=""){
          paramId=this.userid;
          type=1;
        }else{
          paramId=this.courseId;
          type=2;
        }
        if(this.multipleSelection==null || this.multipleSelection.length==0){
          this.$message({
            message: '',
            type: 'warning'
          });
        }else{
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
            center: true
          }).then(()=>{
            for(let [idx,item] of this.multipleSelection.entries()){
              console.log("path"+this.path);
              deleteShareFile(paramId,item.name,this.path,type).then(res=>{
                if(res.data=="alreadyResetPassword"){
                  this.$message({
                    type:'warning',
                    message:''
                  });
                  return;
                }
                if(res.status==200){
                  if(res.data.errorCode==0 && res.data.data=="1"){
                    this.$message({
                      type:"success",
                      message:"!"
                    });
                  }else if(res.data.errorCode==0 && res.data.data=="0"){
                    this.$message({
                      type:"error",
                      message:"!"
                    });
                  }
                }else{
                  this.$message({
                    type:"error",
                    message:"!"
                  });
                }
                this._getShareFileList(paramId,this.path,type);
              })
            }
          })
        }
      },
      // 
      moveFile(){
        this.targetPath="";
        this.moveVisible=true
      },
      // 
      renameFile(){
        if(this.multipleSelection==null || this.multipleSelection.length==0){
          this.$message({
            message: '',
            type: 'warning'
          });
          return;
        }else if(this.multipleSelection!=null && this.multipleSelection.length>1){
          this.$message({
            message: '',
            type: 'warning'
          });
          return;
        }
        this.newFileName=this.multipleSelection[0].name;
        this.renameVisible=true
      },
      _getShareFileList:function (userId,path,type) {
        if(userId=="" && path=="" && type==""){
          if(this.userid!=null && this.userid!=""){
            userId=this.userid;
            type=1;
          }else{
            userId=this.courseId;
            type=2;
          }
          path="/";
          this.path="/";
        }
        console.log("");
        getShareFileList(userId,path,type).then(res=>{
          console.log(res);
          if(res.data=="alreadyResetPassword"){
            this.$message({
              type:'warning',
              message:''
            });
            return;
          }
          if(res.data.errorCode==0){
            if(res.data.data.length==0){
              this.tableData=[];
            }else{
              this.tableData=res.data.data;
            }
          }
        })
        console.log(this.path);
      },
      //
      customUploadHandler:function(item){
        var obj = this;
        var filename=item.file.name;
        this.canUpload=false;
        if(filename.length>80){
          this.$message({
            title: "",
            message: "80!",
            type: 'error'
          });
          this.loading=false;
          this.canUpload=true;
          return;
        }
        if(obj.multipleSelection!=null && obj.multipleSelection.length!=0){
          var type=this.multipleSelection[0].type;
          if(type==2){
            var filePath="";
            if(this.path!="/"){
              this.path=this.path+"/"+this.multipleSelection[0].name;
            }else{
              this.path=this.path+this.multipleSelection[0].name;
            }
          }
        }
        //
        if(this.path.trim().length==0){
          this.path = "/";
        }
        if(this.path.indexOf("/")!=0){
          this.path="/"+this.path;
        }
        this.path = this.path.replace(/\\/g,"/");
        //limit 255100
        //Linux3   80
        console.log(this.path);
        if(this.path.trim() != '/') {
          var dirs = this.path.split("/");
          for (let i = 0; i < dirs.length; i++) {
            var dir = dirs[i];
            if (dir.trim().length> 80) {
              this.$message({
                title: "",
                message: "80!",
                type: 'error'
              });
              this.loading=false;
              this.canUpload=true;
              return;
            } else if (dir && dir.trim().length == 0) {
              //
              this.$message({
                title: "",
                message: "",
                type: 'error'
              });
              this.loading=false;
              this.canUpload=true;
              return;
            }
          }
        }
        //
        var id="";
        //
        var type=0;
        if(this.userid!=null && this.userid!=""){
          id=this.userid;
          type=1;
        }else{
          id=this.courseId;
          type=2;
        }

        this.uploadData={
          userId:id,
          path:this.path
        };
        var url = item.action;
        var file = item.file;
        //200M
        console.log("");
        console.log(file.size);
        if(file.size>20971520){
          this.$message({
            title:"",
            message:"20M",
            type:'error'
          });
          this.loading=false;
          this.canUpload=true;
          return;
        }
        let param = new FormData();
        // 
        param.append('file', file);
        param.append('userId', this.uploadData.userId);
        param.append('path', this.uploadData.path);
        param.append('isCheckExists', "1");
        param.append('type', type);
        axios.post(url, param).then(res => {
          console.log(""+url);
          console.log(res);
          if(res.status==200){
            if(res.data.errorCode==0){
              if(res.data.data=="3"){
                this.$message({
                  type:"error",
                  message:"!"
                });
                this.canUpload=false;
                this.loading=true;
                return;
              }else if(res.data.data=="4"){
                this.$message({
                  type:"error",
                  message:"!"
                });
                this.loading=false;
                this.canUpload=true;
                return;
              }else if(res.data.data=="5"){
                this.$message({
                  type:"error",
                  message:"!"
                });
                this.loading=false;
                this.canUpload=true;
                return;
              }else{
                this.$message({
                  type:"success",
                  message:"!"
                });
                this.canUpload=true;
                this.loading=true;
              }
              //
              obj.$refs.upload.clearFiles();
              //
              this.uploadVisible = false;
              this._getShareFileList(this.uploadData.userId,this.uploadData.path,type);
            }else if(res.data.errorCode==61){
              this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {
                let param = new FormData();
                param.append('file', file);
                param.append('userId', this.uploadData.userId);
                param.append('path', this.uploadData.path);
                param.append('isCheckExists', "2");
                param.append('type', type);
                // 
                axios.post(url, param).then(res => {
                  if(res.status==200) {
                    if (res.data.errorCode == 0) {
                      this.$message({
                        type:"success",
                        message:"!"
                      });
                      this.loading=true;
                      this.canUpload=true;
                      //
                      obj.$refs.upload.clearFiles();
                      //
                      this.uploadVisible = false;
                      this._getShareFileList(this.uploadData.userId, this.uploadData.path,type);
                    }
                  }
                })
              }).catch(()=>{
                this.loading=false;
                this.canUpload=true;
              })
            }
          }
        });
      },
      init:function () {
        console.log(""+this.userid);
        this.actionUrl="/share/uploadShareFile";
        this.path="/";
        var courseId=this.$route.params.courseId;
        console.log(courseId+"");
        if(courseId==null || courseId==undefined || courseId==""){
          this.userid=localStorage['user'] ? JSON.parse(localStorage['user']).userId : '';
          this._getShareFileList(this.userid,this.path,1);
        }else{
          this.courseId=courseId;
          this._getShareFileList(this.courseId,this.path,2);
        }
      },
      //
      getByFileLen:function(val) {
        var len = 0;
        for (var i = 0; i < val.length; i++) {
          var a = val.charAt(i);
          if (a.match(/[^\x00-\xff]/ig) != null)
          {
            len += 3;
          }
          else
          {
            len += 1;
          }
        }
        return len;
      }

  },
    created:function () {
      this.init();
    },
    watch:{
      '$route':function(){
        if(this.$route.fullPath.indexOf("/htSj/share/")>=0){
          this.init();
        }
      }
    }
  }
</script>

<style lang="stylus" lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  .el-collapse-item__header
    color #909399 !important
  .el-input-group__append, .el-input-group__prepend
    color #ffffff
  .el-form-item__content
    font-size 0px
  .title
    width 100%
    height 36px
    line-height 36px
    background-color #f5f5f5
    border-radius 2px
    margin-bottom 20px
    font-size 0px
    display inline-block
    img
      vertical-align middle
      margin-top -10px
      margin-left 15px
    a
      font-size 14px
      color #337ab7
</style>
<style type="text/css">
  #share{
    position: relative;
    top: 0px;
  }
  #Table{
    text-align: center;
  }
  .el-table--border th .cell {
    text-align: center;
    font-weight: bolder;
    font-size: 1.1em;
  }
  .el-dialog__header{
    padding-bottom: 15px;
  }
  #share .el-dialog__body{
    border-top: 1px solid #e5e5e5 ;
    border-bottom: 1px solid #e5e5e5 ;
  }
  .el-dialog__footer{
    margin-top: 20px;
  }
  .el-dialog{
    border-radius: 10px;
  }
  .el-form--inline .el-form-item{
    margin-right: 0px;
  }
  #mulu{
    max-width: calc(100% - 100px);
    display: inline-block;
    margin-left: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: middle;
    margin-top: -10px;
  }
  .el-tooltip__popper.is-dark{
    max-width: 92%;
    padding: 8px 10px ;
    word-break: break-all;
  }
  .el-message-box__status + .el-message-box__message{
    word-break: break-all;
  }
</style>












