<template>
<div>
  <el-dialog title="" id="login"
             :visible.sync="show"
             width="35%"
             center>
    <el-form :model="user">
      <el-form-item style="position: relative">
        <el-input v-model="user.name" placeholder="" auto-complete="off"></el-input>
        <i class=" el-icon--right" style="position: absolute;right: 13px;z-index: 999;top: 10px;">
          <img src="../../../static/images/icon/icon-login-gray.png" alt="">
        </i>
      </el-form-item>
      <el-form-item style="position: relative">
        <el-input v-model="user.pwd" placeholder="" auto-complete="off" ></el-input>
        <i class="el-icon--right" style="position: absolute;right: 13px;z-index: 999;top: 10px;">
          <img src="../../../static/images/icon/icon-pwd.png" alt="">
        </i>
      </el-form-item>
      <p style="float: right;margin-bottom: 20px">
        <a href=""></a>
      </p>
    </el-form>
    <div slot="footer" class="dialog-footer" >
      <el-button type="primary" class="login-btn" @click="_login"></el-button>
    </div>
  </el-dialog>
</div>


  <!---->
</template>

<script>
    import {login,getNotifications,modifyPass,getHotCourse,getSatistics} from '@/common/js/request';
    export default {
      data(){
          return {
            user:{},
            show:false
          }
      },
      methods:{
        loadHomeWork:function(role){
          if(role==1){
            //
          }

        },
        _login:function(){
          //(localStorage)
          login(this.user.username,this.user.password).then(res=>{
            console.log(res);
            if(res.status==200){
              let data = res.data;
              if(data.errorCode==37){
                // alert("");
                this.message.$error="";
                return;
              }
              callbackobjectforjs.logoncallback(this.user.username,this.user.password);
              // 
              let userId = data.data.body.userId;
              let token = data.data.body.token;
              let userRole = data.data.body.role;
              this.user.id = userId;
              this.user.token = token;
              this.user.role = userRole;
              //
              let params;
              switch(userRole){
                case 1:
                  params={
                    userId: userId,
                    role: userRole,
                  };
                  alert(",userId:"+userId+",role:1:");
                  //   
                  break;
                case 2:
                  params={
                    userId: userId,
                    role: userRole
                  };
                  alert(",userId:"+userId+",role:2,:");
                  break;
                case 3:
                  params = {
                    userId: userId,
                    role: userRole //0:student;1:teacher;2:administrator
                  };
                  alert(",userId:"+userId+",role:3,:");
                  break;
              }
              //index.startExperiment.cloudware' 
              //userlocalStorage
              localStorage.setItem("user",JSON.stringify(params));
              localStorage.setItem("token",this.user.token);
              this.$router.push("/all");
            }
          })
        },
        logout:function(){
          //localStoragejssession
          //localStorage
          localStorage.removeItem("user");
          localStorage.clear();
          //
          this.user={};
          //()
          this.$route.push("/main");
        },
        _modifyPass:function(){
          //
          /*modifyPass(userId,oldPassword,newPassword,confirmPassword).then(res=>{
            console.log(res);
          })*/
        },
        _getNotifications:function(){
          //,localStorage
          if(this.user.role!=3){
              getNotifications(this.user.id,this.user.role).then(res=>{
                console.log(res);
              })
          }
        },
        _getHotCourse:function(){
          //()
          getHotCourse().then(res=>{
            console.log(res);
          })
        },
        //,
        _getSatistics:function(){
          getSatistics().then(res=>{
            console.log(res);
          });
         }


      }
    }
</script>

