<template>
    <div class="license">
      <!---->
      <h1 id="title"></h1>
      <el-form :inline="true" id="inlienMain"  class="demo-form-inline" >
        <!--license-->
        <p v-if="noLicese" class="noLicese"></p>
        <!--/license-->
        <p class="userNum" v-if="userShow">: <span>{{userNum}}</span></p>
        <p class="userTime" v-if="userShow" style="text-indent: 14px">: <span>{{userTime}}</span></p>
        <el-button class="button-green" @click="ImportInformation" v-if="importShow"></el-button>
        <el-button class="button-green" @click="updateInformation" v-if="updateShow"></el-button>
      </el-form>
      <!---->

      <el-dialog id="licenseDialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        width="50%">
        <el-form  :rules="dialogRule"  ref="dialogRef" :model="dialogModel" label-width="110px" class="demo-ruleForm">
        <el-form-item label="DEEP" prop="name">
          <el-input type="textarea" v-model="dialogModel.name" readonly  :autosize="{ minRows: 3}"></el-input>
        </el-form-item>
        <el-form-item label="DEEP" prop="license">
          <el-input type="textarea" v-model="dialogModel.license" maxlength="172" placeholder='' :autosize="{ minRows: 3}" ></el-input>
        </el-form-item>
        </el-form>
        <!--<p class="deepTitle sub-title"></p>-->

        <!--<p class="licenseTitle sub-title" prop="license">deep license</p>-->

        <span slot="footer" class="dialog-footer">
           <el-button type="primary" @click="submitDialog"></el-button>
          <el-button @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </div>
</template>

<script type="text/ecmascript-6">
  import {getSysKey,isImportLicense,importOrUpdateLicense} from "@/common/js/request";
  export default {
    data() {
      // license
      var validaTEName = (rule, value, callback) => {
        if (typeof value == 'undefined' || value =="") {
          callback(new Error(''));
        } else if(value.trim().length > 300){
          callback(new Error('300'));
        }  else {
          callback();
        }
      };
      return {
        dialogVisible: false,
        dialogTitle:"",
        userShow:false,
        noLicese:true,
        importShow:true,
        updateShow:false,
        dialogModel:{
          name:'',
          license:''
        },
        dialogRef:'',
        userNum:'',
        userTime:'',
        dialogRule:{
          license: [
            { required: true,validator: validaTEName, trigger: 'blur' }
          ]
        },
        passLicense:''//license
      }
    },
    methods:{
      // license
      ImportInformation(){
        // 
        this.dialogVisible=true,
          // 
        this.dialogTitle=""
        // this.dialogModel.license=""
        // 
        if (this.$refs.dialogRef !==undefined) {
          this.$refs.dialogRef.resetFields();
        }
        // deep
        getSysKey().then(res =>{

          console.log(res.data.data.key)
          this.dialogModel.name=res.data.data.key

        })
      },
      // license
      updateInformation(){
        // 
        this.dialogVisible=true,
          // 
        this.dialogTitle=""
        // this.dialogModel.license=""
        // 
        if (this.$refs.dialogRef !==undefined) {
          this.$refs.dialogRef.resetFields();
          // this.$refs.dialogRef.clearValidate();
        }
        // deep
        getSysKey().then(res =>{
          console.log(res.data.data.key)
          this.dialogModel.name=res.data.data.key
        })

      },
      // 
      submitDialog(){
        this.$refs.dialogRef.validate((valid) => {
          if (valid) {
            //
            isImportLicense().then(res=> {
              if (res.status == 200) {
                // errorCode0
                if (res.data.errorCode == 0) {
                  // 1.  2.
                  // 
                  if(this.dialogTitle==""){
                    importOrUpdateLicense(this.dialogModel.license,1).then(ress=>{
                      //
                      if(ress.status==200){
                        if(ress.data.errorCode==0){
                          this.$message({
                            message: '',
                            type: 'success'
                          });
                          // license
                          this.noLicese=false
                          // license
                          this.userShow=true
                          // 
                          this.importShow=false
                          // 
                          this.updateShow=true
                          this.dialogVisible=false
                          this.userNum=ress.data.data.maxOnlineNum
                          this.userTime=ress.data.data.endTime
                          this.passLicense=this.dialogModel.license
                          this.userTime=ress.data.data.endTime
                          if(this.userTime=="" || this.userTime==undefined){
                            this.userTime=""
                          }else{
                            this.userTime=ress.data.data.endTime
                          }


                        }

                        //
                        else{
                          this.$message({
                            message: ress.data.message,
                            type: 'error'
                          });
                        }
                      }
                    })
                  }
                  //
                  else{
                    this.dialogVisible=false
                    this.$confirm(', ?', '', {
                      confirmButtonText: '',
                      cancelButtonText: '',
                      type: 'warning'
                    }).then(() => {
                      // 
                      isImportLicense().then(updateres=> {
                        if (updateres.status == 200) {
                          if (updateres.data.errorCode == 0) {
                            // 
                            importOrUpdateLicense(this.dialogModel.license, 2).then(resCover => {
                              console.log("")
                              console.log(resCover.data)
                              // 
                              if (resCover.data.errorCode == 0) {
                                this.$message({
                                  message: '',
                                  type: 'success'
                                });
                                    // 
                                    this.noLicese=false
                                    // license
                                    this.userShow=true
                                    // 
                                    this.importShow=false
                                    // 
                                    this.updateShow=true
                                this.userNum = resCover.data.data.maxOnlineNum
                                this.userTime = resCover.data.data.endTime
                                if (this.userTime == "" || this.userTime == undefined) {
                                  this.userTime = ""
                                } else {
                                  this.userTime = resCover.data.data.endTime
                                }
                              }
                              // 
                              else{
                                this.$message({
                                  message: resCover.data.message,
                                  type: 'error'
                                });
                              }
                            })
                          }
                        }
                      })
                    }).catch(() => {
                      this.$message({
                        type: 'info',
                        message: ''
                      });
                    });

                  }
                }
                else if(res.data.errorCode == 95){
                  importOrUpdateLicense(this.dialogModel.license,1).then(ress=>{
                    //
                    if(ress.status==200){
                      if(ress.data.errorCode==0){
                        this.$message({
                          message: '',
                          type: 'success'
                        });
                        // license
                        this.noLicese=false
                        // license
                        this.userShow=true
                        // 
                        this.importShow=false
                        // 
                        this.updateShow=true
                        this.dialogVisible=false
                        this.userNum=ress.data.data.maxOnlineNum
                        this.userTime=ress.data.data.endTime
                        this.passLicense=this.dialogModel.license
                        this.userTime=ress.data.data.endTime
                        if(this.userTime=="" || this.userTime==undefined){
                          this.userTime=""
                        }else{
                          this.userTime=ress.data.data.endTime
                        }


                      }

                      //
                      else{
                        this.$message({
                          message: ress.data.message,
                          type: 'error'
                        });
                      }
                    }
                  })
                }
                else{
                  this.$message({
                    message: res.data.message,
                    type: 'error'
                  });
                }
              }



            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    },

    activated: function(){
      console.log("activated")
      isImportLicense().then(res=>{
        // console.log(res);
        // this.dialogModel.license=""
        // if(resupdate.data.errorCode==0){
        // 
        if(res.data.errorCode==0){
          // license
          this.noLicese=false
          // license
          this.userShow=true
          // 
          this.importShow=false
          // 
          this.updateShow=true
          // this.passLicense=this.dialogModel.license
          this.userNum=res.data.data.maxOnlineNum
          this.userTime=res.data.data.endTime
          if(this.userTime=="" || this.userTime==undefined){
            this.userTime=""
          }else{
            this.userTime=res.data.data.endTime
          }
        }else{
          // license
          this.noLicese=true
          // license
          this.userShow=false
          // 
          this.importShow=true
          // 
          this.updateShow=false
        }
      })
    }
  }
</script>

<style lang="stylus" lang="stylus" rel="stylesheet/stylus" type="text/stylus">
  .license
    #title
      font-size 22px
      margin 20px 3% 0px
      font-family  "Microsoft YaHei"
    #inlienMain
      width 94% !important
      margin 20px auto
      padding-top 20px
      border-top 1px solid #ebeef5
      padding-left 30px
      .userNum,.userTime,.noLicese
        height 40px
        line-height 40px
        font-size 1em
      .noLicese
        margin-bottom 10px
    #licenseDialog
      .el-dialog__body
        padding 8px 20px 10px
        .deepTitle,.licenseTitle
          font-size 16px
          height 40px
          line-height 40px
</style>



