<template>
  <div class="student">
    <h1 id="title"></h1>
    <el-form :inline="true"  id="btn-header" class="demo-form-inline" @submit.native.prevent>
      <el-form-item >
       <!-- <el-button type="primary" @click="_getCustomers()"></el-button>-->
        <el-button type="primary" @click="_Refresh()"></el-button>
        <el-button type="primary" @click="ImportStudent()"></el-button>
        <el-button type="primary" @click="addStudentDialog"></el-button>
        <el-button type="primary" @click="updateDialog"></el-button>
        <el-button type="primary" @click="resetMiMa"></el-button>
        <el-button type="primary" @click="deleteDialog"></el-button>
      </el-form-item>
      <el-form-item style="margin-right: 0px">
        <el-button type="primary" @click="_getCustomers()"></el-button>
      </el-form-item>
      <el-form-item style="float: right">
        <el-input v-model="searchQuery"
                  @keyup.enter.native="select()"
                  placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <!---->
    <div id="table">
      <el-table
        ref="multipleTable"
        tooltip-effect="dark"
        :border="true"
        :data="listData"
        @selection-change="handleSelectionChange"
        style="width: 100%;">
        <el-table-column
          type="selection"
          min-width="140" >
        </el-table-column>
        <!--<el-table-column-->
          <!--prop="id"-->
          <!--label="ID"-->
          <!--:resizable="false">-->
          <!--<template slot-scope="alldata">-->
            <!--<p>{{alldata.row.id}}</p>-->
          <!--</template>-->
        <!--</el-table-column>-->
        <el-table-column
          prop="studentNo"
          label=""
          :resizable="false">
          <template slot-scope="alldata">
            <p >{{alldata.row.studentNo}}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="studentName"
          label=""
          :resizable="false">
          <template slot-scope="alldata">
            <p >{{alldata.row.studentName}}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="grade"
          label="" :resizable="false">
          <template slot-scope="alldata">
            <p >{{alldata.row.grade}}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="gender"
          label="" :resizable="false">
          <template slot-scope="alldata">
            <p >{{alldata.row.gender==1 ? '' : ''}}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="phone"
          label="" :resizable="false">
          <template slot-scope="alldata">
            <p >{{alldata.row.phone}}</p>
          </template>
        </el-table-column>

      </el-table>
    </div>
    <div style="clear: both"></div>
    <div style="max-width:100%;overflow: auto">
      <div id="pagination" style="width:35%;margin:20px 20%">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="p_currentPage"
          :page-sizes="p_pageSizes"
          :page-size="p_pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="p_total">
        </el-pagination>
      </div>
    </div>
    <!---->
    <el-dialog :title="dislogTitle" :visible.sync="dialogFormVisible"
               width="45%" id="editDialog">
      <el-form :model="form"  ref="form" >
        <el-form-item label="" :label-width="formLabelWidth" prop="studentNo">
          <el-input v-model="form.studentNo" placeholder=""
                    :disabled="Isdisabled"
                    @keyup.native.enter="addViseble"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="studentName">
          <el-input v-model="form.studentName"
                    placeholder=""
                    @keyup.native.enter="addViseble"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="studentName">
          <!--<el-input v-model="form.grade" placeholder="" ></el-input>-->

            <el-select  v-model="education" placeholder=""
                        style="float: left;width: 49%"
                        @keyup.native.enter="addViseble">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>


            <el-date-picker style="float: left;width: 50%"
              v-model="selectTime"
              type="year" :editable="false" value-format="yyyy"
              :placeholder="selectPlaceOlder"
              @keyup.native.enter="addViseble">
            </el-date-picker>

        </el-form-item>


       <!-- <el-form-item label="" :label-width="formLabelWidth" prop="gender">
          <el-select  v-model="form.gender"
                      placeholder=""
                      @keyup.native.enter="addViseble">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>-->


        <el-form-item label="       " :label-width="formLabelWidth" prop="gender">
          <el-select v-model="form.gender" placeholder="" style="width: 100%">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
          </el-select>
        </el-form-item>



        <el-form-item label="" :label-width="formLabelWidth" prop="studentName">
          <el-input v-model="form.phone"
                    placeholder=""
                    @keyup.native.enter="addViseble"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addViseble"></el-button>
        <el-button @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog
      id="studentDialog"
      title=""
      :visible.sync="importVisible"
      width="50%"
      @close="xStudentImportDialog()"
      v-if="importVisible" style="margin-top: -5vh">
      <el-upload
        class="upload-demo"
        ref="upload"
        :headers="uploadHeaders"
        :action="batchUrl"
        :auto-upload="false"
        :file-list="fileList"
        :on-success="uploadSuccess"
        :before-upload="beforeUploadHandler"
        accept=".xls,.xlsx"
        :limit="1"
        :on-exceed="handleExceed" v-loading="loading">
        <el-button size="small" type="primary" slot="trigger"></el-button>
        <div slot="tip" class="el-upload__tip">xlsxlsx 20kb<br/>
          <img src="../../../static/images/studentImport.png"/>
        </div>
      </el-upload>
      <el-table :data="errorData"
                v-if="errorData.length>0"
                header-row-class-name="column-left"
                max-height="160px" style="overflow-y: auto">
        <el-table-column
          prop="studentNum"
          label=""
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="studentName"
          label=""
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="reason"
          label=""
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="errorCode"
          label=""
          min-width="180"
          v-if="false">
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
    <el-button @click="closeImportDialog()"></el-button>
    <el-button type="primary" @click="submitUpload()"></el-button>
  </span>
    </el-dialog>
    <!---->
    <div class="copyright">
      <p>     </p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {createStudent, delStudent, getAllStudentInfo, resetPWD, updateStudent} from '@/common/js/request';

  export default{
    data(){
      return{
        form: {
          studentNo:'',
          studentName:'',
          gender:'',
          grade:'',
          phone:''
        },
        listData:[],
        listDataTmp:[],
        listDataFilterTmp: [],
        dialogFormVisible: false,
        formLabelWidth: '80px',
        importVisible:false,
        classId:null,
        batchUrl:null,
        errorData:[],
        couId:"",
        searchQuery:"",
        fileList:[],
        uploadHeaders: {
          'Authorization': 'Bearer ' + (localStorage['token'] ? localStorage['token'] : '')
        },
        // 
        p_currentPage: 1,
        p_pageSizes: [5, 10, 15, 20, 30, 50, 100],
        p_pageSize: 5,
        p_total: 0,
        isAdd:true,
        Isdisabled:false,
        // addRules: {
        //   studentNo: [
        //     { required: true ,message:'', trigger: 'blur'}
        //   ],
        //   studentName: [
        //     { required: true ,message:'', trigger: 'blur'}
        //   ],
        //   gender: [
        //     { required: true,message:'', trigger: 'blur' }
        //   ],
        //   grade: [
        //     { required: true,message:'', trigger: 'blur' }
        //   ],
        //   phone: [
        //     { required: true,message:'', trigger: 'blur' }
        //   ]
        // },
        multipleSelection:[],
        loading:false,
        dislogTitle:'',
        selectTime:'',
        education:'',
        selectPlaceOlder:''
      }
    },
    created() {
      this.batchUrl="/admin/student/batchCreation";
      this._getCustomers();
    },
    activated() {
      this.batchUrl="/admin/student/batchCreation";
      this._getCustomers();
      this.searchQuery=""
    },
   methods:{
     _Refresh:function () {
       this.searchQuery="";
       this._getCustomers();
       // 5
       this.p_pageSize=5
     },
     // 
     _getCustomers: function() {
       if(this.searchQuery.length>45){
         this.$message({
           type:'error',
           message:'45'
         })
         return;
       }

       // 5
       this.p_pageSize=5

       getAllStudentInfo().then(res=>{
         if(res.data=="alreadyResetPassword"){
           this.$message({
             type:'warning',
             message:''
           });
           return;
         }
         this.listDataTmp=res.data.data.studentList;
         this.listDataTmp.forEach((value) =>{
           // console.log(":"+typeof value.grade)
           //
           if(typeof value.grade !="object"){
             // if(form.teacherContact.indexOf("@") != -1){
             //
             if(value.grade.indexOf("") ==-1){
               value.grade = value.grade +""
             }
           }

         })
         this._filterData();
         this._initPageInfo();
       })
     },
     _validateForm: function (form) {
       // 
       let errorMsg = null
       if (!form.studentNo || form.studentNo.trim().length === 0) {
         errorMsg = ''
       }
       else if (form.studentNo.trim().length > 45) {
         errorMsg = '45'
       }else if((/^\s+/).test(form.studentNo)){
         errorMsg = ''
       }else if((/\s+$/).test(form.studentNo)){
         errorMsg = ''
       }
       else {
         var regex = /^\+?[0-9]\d*$/;
         if(!regex.test(form.studentNo.trim())){
           errorMsg = "0";
         }
       }
       if (errorMsg && errorMsg.trim().length > 0) {
         this.$message.error(errorMsg)
         return false
       }
       // 
       if (!form.studentName || form.studentName.trim().length === 0) {
         errorMsg = ''
       } else if (form.studentName.trim().length > 45) {
         errorMsg = '45'
       }else if((/^\s+/).test(form.studentName)){
         errorMsg = ''
       }else if((/\s+$/).test(form.studentName)){
         errorMsg = ''
       }
       if (errorMsg && errorMsg.trim().length > 0) {
         this.$message.error(errorMsg)
         return false
       }
       //
       if(!this.education || this.education.trim().length==0){
          errorMsg='';
       }
       if (errorMsg && errorMsg.trim().length > 0) {
         this.$message.error(errorMsg)
         return false
       }
       if(this.selectTime==undefined || !this.selectTime || this.selectTime.length==0){
         errorMsg='';
       }
       if (errorMsg && errorMsg.trim().length > 0) {
         this.$message.error(errorMsg)
         return false
       }
       // 
       if (!form.gender) {
         errorMsg = ''
       }
       if (errorMsg && errorMsg.trim().length > 0) {
         this.$message.error(errorMsg)
         return false
       }
      //
      //  var email=/^[A-Za-z0-9\u4e00-\u9fa5\.]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
       var email=/^[A-Za-z0-9\u4e00-\u9fa5+\.+\-+\_]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
       var phone= /^1\d{10}$/;
       var kong=/^\S+$/;
       if(!form.phone || form.phone.trim().length === 0){
         errorMsg=""
       }else if(!kong.test(form.phone)){
         errorMsg=""
       } else if (form.phone.trim().length > 45) {
         errorMsg="45"
       }else{
         if(email.test(form.phone) ||  phone.test(form.phone)){
           return true
         }
         else{
           if(form.phone.indexOf("@") != -1){
             if(!email.test(form.phone)){
               errorMsg=""
             }
           }
           if(!phone.test(form.phone)){
             errorMsg=""
           }
         }
       }
       if (errorMsg && errorMsg.trim().length > 0) {
         this.$message.error(errorMsg)
         return false
       }
       return true
     },
     xStudentImportDialog: function(){
       this.importVisible = false;
       this._getCustomers();
     },
     select: function () {
       if(this.searchQuery.length>45){
         this.$message({
           type:'error',
           message:'45'
         })
         return;
       }
       this._filterData();
       this._initPageInfo();
     },
     _filterData: function () {
       if (this.searchQuery === '') {
         this.listDataFilterTmp = this.listDataTmp;
         return
       }
       let filterTableDataEnd = [];
       this.listDataTmp.forEach((value, index) => {
         var genderName = '';
         if(value.gender == 1){
           genderName = '';
         }else if(value.gender == 2){
           genderName = '';
         }
         if (
           // (value.id && (value.id+'').indexOf(this.searchQuery) >= 0) ||
           (genderName && genderName.indexOf(this.searchQuery) >= 0) ||
           (value.studentNo && value.studentNo.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0) ||
           (value.studentName && value.studentName.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0)||
           (value.grade && value.grade.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0)||
           (value.phone && value.phone.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0)

         ) {
           filterTableDataEnd.push(value)
         }
       });
       this.listDataFilterTmp = filterTableDataEnd
     },
     _initPageInfo: function () {
       this.p_total = this.listDataFilterTmp.length;
       this.p_currentPage = 1;
       this.listData = this.listDataFilterTmp.slice(0, this.p_pageSize);
     },
     // 
     handleSelectionChange(val) {
       this.multipleSelection = val;
     },
     // 
     closeImportDialog(){
       this.importVisible=false;
       this._Refresh();
       //this._getCustomers();
     },
     submitUpload(){
         //
       this.$refs.upload.submit();
     },
      // 
     ImportStudent(){
       this.importVisible=true;
        this.errorData=[]
     },
     handleExceed(files, fileList) {
       this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
     },
     beforeUploadHandler:function(file){
       //
       var fileName = file.name;
       if(fileName.lastIndexOf(".xls")<0 && fileName.lastIndexOf(".xlsx")<0 ){
         this.$message({
           title:"",
           message:"xlsxlsx",
           type:"error"
         });
         this.loading=false;
         return false;
       }
       if(file.size>1024*20){
         this.loading = false;
         this.$message({
           title:"",
           message:"20KB",
           type:"error"
         });
         this.loading=false;
         return false;
       } else {
         this.loading = true;
         return true;
       }
     },
     uploadSuccess:function(response){
       this.loading = false;
       if(response.errorCode==0){
         this.$refs.upload.clearFiles();
         this.errorData = response.data.failureReasonList;
         if(this.errorData.length==0){
            this.$message({
              type:'success',
              message:'!'
            });
           this.importVisible = false;
           this.fileList=[];
         }else {
           for(var i = 0;i < this.errorData.length;i++){
             var data = this.errorData[i];
             if(data.errorCode == 29){
               data.reason = (data.reason.split(""))[0];
             }
           }
         }
         this._Refresh();
         //this._getCustomers();
       }else {
         this.importVisible = false;
         this.fileList=[];
         this.$message({
           title:"",
           message:response.message,
           type:"error"
         });
         this._Refresh();
       }
     },
     // 
     addStudentDialog(){
       //
       this.dislogTitle=""
       this.form = {};
       this.dialogFormVisible = true;
       this.isAdd=true;
       this.Isdisabled=false;
       this.education="";
       this.selectTime="";
     },
     // 
     updateDialog(){
       if(this.multipleSelection.length==0){
         this.$message({
           message: '',
           type: 'warning'
         });
         return;
       } else if(this.multipleSelection.length!=1){
         this.$message({
           message: '',
           type: 'warning'
         });
         return;
       }else{
         this.dislogTitle="";
        // var selectDate=this.multipleSelection[0];
/*
         selectDate.studentNo=selectDate.studentNo;
         selectDate.studentName=selectDate.studentName;
         selectDate.grade=selectDate.grade;
         selectDate.gender=selectDate.gender;
         selectDate.phone=selectDate.phone;*/
         if(this.multipleSelection[0].gender==1){
           this.form.gender=this.multipleSelection[0].gender+"";
         }else if(this.multipleSelection[0].gender==2){
           this.form.gender=this.multipleSelection[0].gender+"";
         }
         this.dialogFormVisible = true;
         if(this.multipleSelection[0].grade!=null && this.multipleSelection[0].grade!=""){
           var grade=this.multipleSelection[0].grade.substr(0,2);
           var year=this.multipleSelection[0].grade.substr(2,this.multipleSelection[0].grade.length);
            this.education=grade;
            this.selectTime=year;
         }
         console.log(this.multipleSelection[0].gender);
         Object.assign(this.form,this.multipleSelection[0]);
         this.isAdd=false;
         this.Isdisabled=true;
       }

     },
     // 
     addViseble: function () {
       if (!this._validateForm(this.form)) {
         return;
       }
       if(this.isAdd){
         //
         this.form.grade=this.education+this.selectTime;
         //console.log(this.form.grade);
         createStudent(this.form).then(res=>{
           console.log(res);
           if (res.data.errorCode === 0) {
             this.$message({
               type: 'success',
               message: '!'
             });
             this._Refresh();
             //this._getCustomers();
           } /* else if(res.data.errorCode === 65){
             this.$message({
               type: 'error',
               message: res.data.data.message
             });
             this._Refresh();
           }else if(res.data.errorCode === 28){
             this.$message({
               type: 'error',
               message: res.data.data.message
             });
             this._Refresh();
           }*/
           else {
             console.log(":"+res.data.errorCode)
             this.$message({
               type: 'error',
               message: res.data.message
             });
             this._Refresh();
           }
           this.dialogFormVisible = false;
         });
       }else{
         //
         this.form.grade=this.education+this.selectTime;
         // console.log(":"+typeof this.form.gender)
         // if(this.form.gender==""){
         //   this.form.gender=1
         // }else{
         //   this.form.gender=2
         // }
         updateStudent(this.form).then(res=>{
           if (res.data.errorCode === 0) {
             this.$message({
               type: 'success',
               message: '!'
             });
             this._Refresh();
            // this._getCustomers();
           } else {
             this.$message({
               type: 'error',
               message: res.data.message
             });
             this._Refresh();
           }
           this.dialogFormVisible = false;
         });

       }
     },
     // 
     resetMiMa(){
       console.log(this.multipleSelection)
       if(this.multipleSelection.length==0){
         this.$message({
           message: '',
           type: 'warning'
         });
       }else{
         console.log(this.multipleSelection)
         this.$confirm(', ?', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           type: 'warning'
         }).then(() => {
           for(let [idx,item] of this.multipleSelection.entries()){
             let userId=item.id;
             resetPWD(userId).then(res=>{
               if(res.data.errorCode==0){
                 this.$message({
                   type: 'success',
                   message: '!  12345678'
                 });
                 this._Refresh();
               }
             });
           }
         }).catch(() => {
         });
       }
     },
     // 
     deleteDialog(){
       // debugger
       console.log(this.multipleSelection)
       console.log("-----classId:------"+this.classId)
       if(this.multipleSelection.length==0){
         // alert("")
         this.$message({
           message: '',
           type: 'warning'
         });
       }
       else{
         this.$confirm(', ?', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           type: 'warning',
           center: true
         }).then(() => {
           for(let [idx,item] of this.multipleSelection.entries()){
             delStudent(item.studentNo).then(res => {
               console.log(res);
               if(res.status==200){
                 if(res.data.errorCode ==0){
                   if(idx==this.multipleSelection.length-1) {
                     this.$message({
                       type: 'success',
                       message: '!'
                     });
                     this._Refresh();
                     //this._getCustomers();
                   }
                 } else {
                   // 
                   this.$message({
                     type: 'error',
                     message: res.data.message
                   });
                   this._Refresh();
                 }
               }
             })
           }
         }).catch(() => {
         });
       }

     },
     handleCurrentChange(val){
       this.p_currentPage = val;
       this.listData = this.listDataFilterTmp.slice((val - 1) * this.p_pageSize, (val) * this.p_pageSize)
     },
     handleSizeChange(val){
       this.p_pageSize = val;
       this._initPageInfo()
     }
   },
    beforeRouteEnter: (to, from, next) => {
      console.log("");
      next(vm => {
        vm.p_currentPage=1
        vm.handleSizeChange(5)
      });
    }
    // watch
    // watch: {
    //   searchQuery(){
    //     if(this.searchQuery.length>45){
    //       this.$message({
    //         title:"",
    //         message:"45",
    //         type:"error"
    //       });
    //     }
    //   }
    // }
  }
</script>

<style lang="stylus" lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>

