      .block
        border 1px solid red
    .el-dialog
      width 50%
    #table
      text-align center
      .el-table--border th .cell
        text-align center
    .copyright
      text-align right
      color #b4b4b4
      font-size 14px
      margin-top 20px
      margin-right 3%
</style>
<style type="text/css" >
  #btn-header .el-form-item:not(:nth-child(1)){
    float: right;
  }
  .el-table--border th .cell {
    text-align: center;
    font-weight: bolder;
    font-size: 1.1em;
  }
  #editDialog .el-form-item{
    width: 80%;
    margin: 0px auto 20px;
  }
  /**/
  #editDialog .el-form-item__label{
    text-align: left;
  }
  /*studentDialog*/
  /**/
  #studentDialog .el-table--group::after,
  #studentDialog .el-table--border::after,
  #studentDialog .el-table::before {
    background-color: transparent;
  }
</style>



