<template>
  <div class="xueqi">
    <h1 id="title"></h1>
    <el-form :inline="true"  id="btn-header" class="demo-form-inline" @submit.native.prevent>
      <el-form-item >
        <!--<el-button class="button-green" @click="_getListData()"></el-button>-->
        <el-button type="primary" @click="_Refresh()"></el-button>
        <el-button class="button-green" @click="addTerm()"></el-button>
        <el-button class="button-green" @click="updateTerm()"></el-button>
        <el-button class="button-green" @click="deleteTerm()"></el-button>
        <!--<el-button type="primary" @click="_getListData()"></el-button>-->
        <!--<el-button type="primary" @click="addTerm()"></el-button>-->
        <!--<el-button type="primary" @click="updateTerm()"></el-button>-->
        <!--<el-button type="primary" @click="deleteTerm()"></el-button>-->
      </el-form-item>
      <!--<el-form-item label="" style="float: right;margin-right: 0px;">-->
        <!--<el-input v-model="searchQuery" @keyup.enter.native="select()" placeholder=""></el-input>-->
      <!--</el-form-item>-->
      <el-form-item style="margin-right: 0px">
        <el-button type="primary" @click="_getListData()"></el-button>
      </el-form-item>
      <el-form-item >
        <el-input v-model="searchQuery" placeholder=""
                  @keyup.native.enter="select()"></el-input>
      </el-form-item>
    </el-form>
    <!---->
    <div id="Table">
      <el-table
        ref="multipleTable"
        tooltip-effect="dark"
        center
        :data="listData"
        :border="true"
        style="width: 100%;"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="id"
          label=""
          width="" :resizable="false">
          <template slot-scope="alldata">
            <p >{{ alldata.row.id }}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="year"
          label=""
          width=""
          :resizable="false"
          show-overflow-tooltip>
          <template slot-scope="alldata">
            <p >{{ alldata.row.year.split('-').length==1 ? alldata.row.year.concat('-').concat(parseInt(alldata.row.year)+1) : alldata.row.year}}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="semester"
          width=""
          label=""
          :resizable="false"
          show-overflow-tooltip>
          <template slot-scope="alldata">
            <p >{{ alldata.row.semester==1 ? '' : alldata.row.semester==2 ? '' : '' }}</p>
          </template>
        </el-table-column>

        <!--<el-table-column label="" min-width="140" :resizable="false">-->
          <!--<template slot-scope="scope" >-->
            <!--<el-button-->
              <!--size="mini"-->
              <!--@click="showEdit(scope.$index)"></el-button>-->
            <!--<el-button-->
              <!--size="mini"-->
              <!--type="danger"-->
              <!--@click="deletedata(scope.$index)"></el-button>-->
          <!--</template>-->
        <!--</el-table-column>-->
      </el-table>
    </div>
    <div style="clear: both"></div>
    <div id="pagination" style="width:35%;margin:20px 20%;">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="p_currentPage"
        :page-sizes="p_pageSizes"
        :page-size="p_pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="p_total">
      </el-pagination>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form" :rules="addRules">
        <el-form-item label="" :label-width="formLabelWidth" prop="year">
          <el-input v-model="form.year" placeholder='"2017""2017-2018"' auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="semester">
          <el-select v-model="form.semester" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button class="button-green" @click="save()"></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title=""
               :visible.sync="bianjiFormVisible"
               width="40%">
      <el-form :model="bianjiForm" :rules="editRules">
        <el-form-item label="" :label-width="formLabelWidth" prop="year">
          <el-input v-model="bianjiForm.year" placeholder='"2017""2017-2018"' auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="semester">
          <el-select v-model="bianjiForm.semester" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bianjiFormVisible = false"> </el-button>
        <el-button class="button-green" @click="updateInfo()"></el-button>
      </div>
    </el-dialog>
    <!---->
    <div class="copyright">
      <p>     </p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {getXueQiListData, deleteXueQi, get, post,updateTearm,getTearmById} from '@/common/js/request';
  export default{
    data() {
      return {
        listData: [],
        listDataTmp: [],
        listDataFilterTmp: [], // 
        form: {
          year: '',
          semester: ''
        },
        bianjiForm:{
          year:'',
          semester:''
        },
        bianjiFormVisible: false,
        dialogFormVisible: false,
        formLabelWidth: '100px',
        multipleSelection: [],//
        searchQuery:'',//
        handleEdit: '',
        // 
        p_currentPage: 1,
        p_pageSizes: [5, 10, 15, 20, 30, 50, 100],
        p_pageSize: 5,
        p_total: 0,
        addRules :{
          year :[{ required: true,message: '',min: 1, max: 45, trigger: 'blur' }],
          semester :[{ required: true,message: '', trigger: 'blur' }]
        },
        editRules:{
          year :[{ required: true, message: '',trigger: 'blur' }],
          semester :[{ required: true, message: '',trigger: 'blur' }]
        }
      }
    },
    activated () {
      this.searchQuery="";
      this._getListData()
    },
    methods: {
      //
      _Refresh:function () {
        this.searchQuery="";
        this._getListData()
        // 5
        this.p_pageSize=5
      },
      // 
      _getListData: function () {
        if(this.searchQuery.length>45){
          this.$message({
            type:'error',
            message:'45'
          })
          return;
        }

        // 5
        this.p_pageSize=5

        getXueQiListData().then(res => {
          this.listDataTmp = res.data.data.semesterList;
          // this.listDataTmp.forEach((value, index) => {
          //   if(value.year.split('-').length==1){
          //     value.year=value.year+"-"+(parseInt(value.year)+1)
          //   }
          // })
          this._filterData()
          this._initPageInfo()
        })
      },
      // 
      _initPageInfo: function () {
        this.p_total = this.listDataFilterTmp.length
        this.p_currentPage = 1
        this.listData = this.listDataFilterTmp.slice(0, this.p_pageSize)
      },
      // filter data
      _filterData: function () {
        if (this.searchQuery === '') {
          this.listDataFilterTmp = this.listDataTmp
          return
        }
        let filterTableDataEnd = []
        this.listDataTmp.forEach((value, index) => {
//          alldata.row.semester==1 ? '' : alldata.row.semester==2 ? '' : ''
          value.semesterDes = value.semester === 1 ? '' : value.semester === 2 ? '' : ''
          if (
            (value.id && (value.id + '').indexOf(this.searchQuery) >= 0) ||
            (value.year && value.year.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0) ||
            (value.semesterDes && value.semesterDes.toLowerCase().indexOf(this.searchQuery.toLowerCase()) >= 0)
          ) {
            filterTableDataEnd.push(value)
          }
        });
        this.listDataFilterTmp = filterTableDataEnd
      },
      // 
      handleSizeChange(val) {
        this.p_pageSize = val
        this._initPageInfo()
      },
      handleCurrentChange(val) {
        this.p_currentPage = val
        this.listData = this.listDataFilterTmp.slice((val - 1) * this.p_pageSize, (val) * this.p_pageSize)
      },
      // 
      select: function () {
        if(this.searchQuery.length>45){
          this.$message({
            type:'error',
            message:'45'
          })
          return;
        }
        this._filterData()
        this._initPageInfo()
      },

      addTerm:function(){
        this.form = {};
        this.dialogFormVisible = true;
      },
      // 
      handleSelectionChange(val) {
        this.multipleSelection = val;
        // console.log(value.length)
      },
      // 
      save: function () {
        var regPos = /^\d+$/
        if (!regPos.test(this.form.year)) {
          this.$message({
            type: 'error',
            message: ''
          })
          return;
        }
        // else if(this.form.year.trim().length >45){
        //   this.$message({
        //     type: 'error',
        //     message: '45'
        //   })
        //   return;
        // }
        else if(this.form.year.trim().indexOf("-") >0){
          if(this.form.year.trim().length>9){
            this.$message({
              type: 'error',
              message: '9'
            })
            return;
          }
        }else if(this.form.year.trim().length>4){
          this.$message({
            type: 'error',
            message: '4'
          })
          return;
        }
        if(!this.form.semester){
          this.$message({
            type: 'error',
            message: ''
          })
          return;
        }
        this.form.description = this.form.year + '' + this.form.semester + ''
        post('/admin/semester/creation', this.form).then(res => {
          if (res.status === 200) {
            if(res.data.errorCode == 0){
              this.$message({
                type: 'success',
                message: ''
              });
              this.dialogFormVisible = false;
              this._getListData();
            } else {
              this.$message({
                type: 'error',
                message: res.data.message
              });
            }
          } else {
            this.$message({
              type:'error',
              message:''
            });
            this.dialogFormVisible = false;
          }

        })
      },
      // 
      // showEdit(index) {
      //   console.log(this.listData[index]);
      //   //
      //   let id = this.listData[index].id;
      //   getTearmById(id).then(res => {
      //     if(res.data.data==null){
      //       this.$message({
      //         type:'error',
      //         message:'!'
      //       });
      //       this._getListData();
      //     }else{
      //       this.bianjiFormVisible = true;
      //       this.listData[index].semester+="";
      //       Object.assign(this.bianjiForm,this.listData[index]);
      //     }
      //   })
      // },
      // 
      updateInfo() {
        var regPos = /^\d+$/
        if (!regPos.test(this.bianjiForm.year)) {
          this.$message({
            type: 'error',
            message: ''
          })
          return;
        }
       else if(this.bianjiForm.year.trim().length>4){
          this.$message({
            type: 'error',
            message: '4'
          })
          return;
        }
        updateTearm(this.bianjiForm).then(res => {
          if (res.status === 200) {
            if(res.data.errorCode == 0){
              this.$message({
                type: 'success',
                message: ''
              });

            } else {
              this.$message({
                type: 'error',
                message: res.data.message
              });
            }

            this._getListData()
          } else {
            this.$message({
              type:'error',
              message:''
            })
          }
          this.bianjiFormVisible = false;
        })
      },
      // 
      // deletedata(index) {
      //   this.$confirm(', ?', '',
      //     {
      //     confirmButtonText: '',
      //     cancelButtonText: '',
      //     type: 'warning'
      //   }).then((res) => {
      //     let id = this.listData[index].id;
      //     deleteXueQi(id).then(res => {
      //       // console.log(this.listData.id)
      //       if(res.status==200){
      //         if(res.data.errorCode!=0){
      //           // 
      //           this.$message({
      //             type: 'error',
      //             message: res.data.message
      //           });
      //         } else {
      //           this._getListData();
      //           // 
      //           this.$message({
      //             type: 'success',
      //             message: '!'
      //           });
      //         }
      //       }
      //     })
      //   }).catch(() => {
      //     this.$message({
      //       type: 'info',
      //       message: ''
      //     });
      //   });
      // },
      // 
      updateTerm(){
        // debugger
        if (this.multipleSelection.length === 1) {
          // 
          this.bianjiFormVisible= true;
          // var check = this.multipleSelection;
          //   let id =check[v].id;
          // console.log(check)
          // getTearmById(id).then(res => {
          //   if(res.data.data==null){
          //     this.$message({
          //       type:'error',
          //       message:'!'
          //     });
          //     this._getListData();
          //   }else{
          //     this.bianjiFormVisible = true;
          //     this.listData[id].semester+="";
          //     Object.assign(this.bianjiForm,this.listData[id]);
          //   }
          // })
          // // 
          // Object.assign(this.bianjiForm,this.multipleSelection[0]);
          //
          // let id = this.listData[index].id;
          let id = this.multipleSelection[0].id;
          getTearmById(id).then(res => {
            if(res.data.data==null){
              this.$message({
                type:'error',
                message:'!'
              });
              this._getListData();
            }else{
              this.bianjiFormVisible = true;
              this.multipleSelection[0].semester+="";
              // Object.assign(this.bianjiForm,this.listData[0]);
              Object.assign(this.bianjiForm,this.multipleSelection[0]);
            }
          })
          console.log(check)
        }
        else {
          // alert(",")
          this.$message({
            message: '',
            type: 'warning'
          });
        }
      },
      // 
      deleteTerm() {
        // var check = this.multipleSelection;
        // if (check.length == 0)
        //   return this.$message.error('');
        // for (var v = 0; v<check.length; v++){
        //   debugger
        //   let id =check[v].id;
        //   deleteXueQi(id);
        // }
        // this._getListData();
        // // 
        // this.$message({
        //   type: 'success',
        //   message: '!'
        // });
        if(this.multipleSelection.length==0){
          // alert("")
          this.$message({
            message: '',
            type: 'warning'
          });
        }
        else{
          // debugger
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
            center: true
          }).then(() => {
            for(let [idx,item] of this.multipleSelection.entries()){
              //console.log(item.id);
              deleteXueQi(item.id).then(res => {
                console.log("------res-------"+res);
                if(res.status==200){
                  console.log(res.data.errorCode)
                  if(res.data.errorCode!=0){
                    // 
                    this.$message({
                      type: 'error',
                      message: res.data.message
                    });
                  } else {
                    if(idx==this.multipleSelection.length-1) {
                      this.$message({
                        message: '',
                        type: 'success'
                      });
                      this._getListData();
                    }
                  }
                }
              })
            }
          }).catch(() => {
            // this.$message({
            //   type: 'info',
            //   message: ''
            // });
          });
        }
      }
    },
    beforeRouteEnter: (to, from, next) => {
      console.log("");
      next(vm => {
        // vm._getExpirementInfo();
        // vm.handleSizeChange(5)
        vm.p_currentPage=1
      });
    }
    // watch
    // watch: {
    //   searchQuery(){
    //     if(this.searchQuery.length>45){
    //       this.$message({
    //         title:"",
    //         message:"45",
    //         type:"error"
    //       });
    //     }
    //   }
    // }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  #btn-header .el-form-item:not(:nth-child(1)){
    float: right;
  }
  .xueqi
    #title
      font-size 22px
      margin 20px 3% 0px
      font-family  "Microsoft YaHei"
    .demo-form-inline
      width 94%
      margin 20px auto
      padding-top 20px
      border-top 1px solid #ebeef5 !important
    .el-dialog
      width 30%
    #Table
      width 94%
      height auto
      margin 0px auto
      text-align: center
      th
        .cell
          text-align center
          font-weight: bolder;
          font-size: 1.1em;
    .copyright
      text-align right
      color #b4b4b4
      font-size 14px
      margin-top 20px
      margin-right 3%
</style>
<style type="text/css" >
  .el-table--border th .cell {
    text-align: center;
    font-weight: bolder;
    font-size: 1.1em;
  }
  .xueqi .el-table .cell{
    white-space: nowrap;
    overflow: hidden;
    word-break:break-all;
    text-overflow: ellipsis;
    padding-right: 10px;
  }
</style>



